import React from 'react'
import logo from './logo.svg';
import './App.css';

class App extends React.Component{
   constructor(props){
        super(props);
        this.state = {
            holis:"no hay"
        };
    }

  render(){
    let context = this;
   let recupDown = async () => {
        //Ruta del servicio web
        let url = "https://3lb138e355.execute-api.us-east-1.amazonaws.com/holis/";

        //En este caso solo se envía la url y el servidor responde con un json
        await fetch(url,{
          mode: 'cors', // no-cors, *cors, same-origin
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
                  "Access-Control-Allow-Headers" : "Content-Type",
                  "Access-Control-Allow-Origin": "https://www.example.com",
                  "Access-Control-Allow-Methods": "OPTIONS,POST,GET"
          },
        })
            .then(res => res.json())
            
            .then(
                (result) => {
                    //al recibir la respuesta se envian las descargas a downloads en el estado
                    console.log(result)
                    context.setState({
                        holis: result.body
                    });

                    console.log(result)
                },
                // Nota: es importante manejar errores aquí y no en 
                // un bloque catch() para que no interceptemos errores
                // de errores reales en los componentes.
                (error) => {
                    console.log(error)
                }
            )
    }

    let holi=()=>{
      context.setState({
        holis: "ppwe"
      });
    }

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <button onClick={()=>recupDown()}>Recuperar datos</button>
        <label>{context.state.holis}</label> 
      </header>
    </div>
  );
}
}
export default App;
